.commentsContainer{max-width:800px;margin:0 auto;padding:20px;font-family:Arial,sans-serif}.commentsList{list-style-type:none;padding:0}.commentItem{background-color:#f9f9f9;border:1px solid #e0e0e0;border-radius:4px;padding:15px;margin-bottom:15px}.commentHeader{display:flex;justify-content:space-between;align-items:center;margin-bottom:10px}.commentAuthor{font-weight:bold}.commentDate{font-size:.8em;color:#666}.commentText{margin-bottom:10px}.commentActions{display:flex;justify-content:flex-end;gap:10px}.button{padding:5px 10px;border:none;border-radius:4px;cursor:pointer;font-size:.9em;transition:background-color .3s}.editButton{background-color:#f0f0f0;color:#333}.editButton:hover{background-color:#e0e0e0}.deleteButton{background-color:#ff6b6b;color:#fff}.deleteButton:hover{background-color:#ff4757}.commentForm{margin-top:20px}.commentTextarea{width:100%;padding:10px;border:1px solid #e0e0e0;border-radius:4px;resize:vertical;min-height:100px;margin-bottom:10px}.submitButton{background-color:#4caf50;color:#fff;padding:10px 15px;border:none;border-radius:4px;cursor:pointer;font-size:1em}.submitButton:hover{background-color:#45a049}